.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.navbar.navbar-blue {
  background-color: #017fff;
  margin-bottom: 0;
}

.navbar-header {
  padding: 7px 0;
}

.navbar-brand {
  float: left;
  padding: 10px 15px;
  font-size: 30px;
  line-height: 20px;
  background: transparent;
  border: none;
  font-weight: 600;
  color: #fff;
  letter-spacing: 2px;
}

button.header-link.login-btn {
  background: transparent;
  border: none;
  color: #fff;
  font-size: 18px;
  padding: 22px 0;
  margin-right: 20px;
}

button.header-link.trial-btn {
  margin: 19px 0 19px 15px;
  padding: 6px 10px;
  background: black;
  color: #fff;
  border: none;
}

.navbar-brand:focus,
button.header-link:focus {
  outline: none;
}

.navbar-toggle .icon-bar {
  background-color: #fff;
}

.top-header .nav > li {
  position: relative;
  display: block;
  text-align: center;
}

.bottom-header {
  background-color: #000;
  padding: 15px 0;
  color: #fff;
  box-shadow: 0px 3px 8px 1px #474545;
}

.content-title {
  font-size: 23px;
  font-weight: 600;
}

.app-footer {
  background-color: #000;
  padding: 30px 0;
  margin-top: auto;
  color: #e3e0e0;
}

.footer-nav-links li {
  display: inline-block;
  border-right: 1px solid #e3e0e0;
  padding: 0 15px;
  line-height: 15px;
}

ul.footer-nav-links li:last-child {
  border-right: none;
}

ul.footer-nav-links {
  margin-left: -15px;
  padding: 0;
}

.social-icon-wrapper img {
  margin-right: 25px;
}

.store-img-wrapper img {
  margin-right: 20px;
}

.store-img-wrapper img:last-child {
  margin-right: 0;
}

.bottom-images {
  margin-top: 40px;
}

.copyright-text {
  font-size: 12px;
  color: #c5c5c5;
}

.stream-category .item {
  width: 130px;
  height: 190px;
  background-color: #000;
  float: left;
  margin-right: 20px;
  padding: 48px 0px;
  position: relative;
}

.stream-category {
  margin: 40px 0;
}

.item-title {
  color: #fff;
  text-align: center;
  font-size: 20px;
  text-transform: capitalize;
  margin-top: -15px;
}

.outer-title {
  position: absolute;
  bottom: -25px;
  font-size: 15px;
  font-weight: 500;
  text-transform: capitalize;
}

.stream-list {
  margin: 40px 0;
  overflow: overlay;
}

.stream-list .item {
  width: 100px;
  height: 150px;
  float: left;
  margin: 0 20px 40px 0;
}

.stream-list img {
  width: 100%;
}

.stream-title {
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: ellipsis;
  padding-top: 5px;
  font-weight: 500;
}

@media (max-width: 768px) {
  .store-img-wrapper {
    text-align: left !important;
    margin-top: 30px;
  }

  .store-img-wrapper img {
    margin-right: 10px;
  }

  .store-img-wrapper img:last-child {
    margin-right: 0;
  }
}

@media (max-width: 500px) {
  .store-img-wrapper img {
    width: 80px;
  }
}
